import {
  GET_ORDERS,
  GET_ORDERS_STATUS,
  CREATE_ORDER,
  EDIT_PAGE,
  GET_ORDERS_AMOUNT,
  EDIT_PRICE,
  EDIT_CAR,
  EDIT_STATUS,
  EDIT_CITY,
  EDIT_DATE_FROM,
  EDIT_DATE_TO,
  EDIT_RATE,
  EDIT_POINT,
  EDIT_TANK,
  EDIT_CHAIR,
  EDIT_WHEEL,
  SET_CAR,
  SET_CITY, 
  SET_STATUS
} from "../reducers/orders";
import api from "../../components/api";

export const getOrders = (page, filter) => async (dispatch) => {
  try {
    await api
      .get(`db/order?&limit=3&page=${page}${filter}`, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        dispatch({ type: GET_ORDERS_AMOUNT, payload: res.data.count });
        return res;
      })
      .then((res) => dispatch({ type: GET_ORDERS, payload: res.data.data }));
  } catch (e) {
    console.error(e);
  }
};

export const getOrdersStatus = () => async (dispatch) => {
  try {
    await api
      .get(`db/orderStatus`, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) =>
        dispatch({ type: GET_ORDERS_STATUS, payload: res.data.data })
      );
  } catch (e) {
    console.error(e);
  }
};

export const getOrdersAmount = (amount) => {
  return {
    type: GET_ORDERS_AMOUNT,
    payload: amount,
  };
};

export const createOrder = (order) => {
  return {
    type: CREATE_ORDER,
    payload: order,
  };
};

export const deleteOrder = (id) => async (dispatch) => {
  try {
    await api.delete(`db/order/${id}`).finally((res) => {
      dispatch(
        createOrder({
          orderStatusId: {
            id: "",
            name: "",
          },
          cityId: {
            id: "",
            name: "",
          },
          pointId: {
            id: "",
            cityId: "",
            address: "",
            name: "",
          },
          carId: {
            id: "",
            name: "",
            priceMin: 0,
            priceMax: 0,
            description: "",
            colors: [],
            thumbnail: {
              path: "",
              size: 0,
              mimetype: "",
              originalname: "",
            },
            categoryId: {
              id: "",
              name: "",
            },
          },
          color: "",
          dateFrom: new Date(),
          dateTo: new Date(),
          rateId: {
            rateTypeId: {
              id: "",
              name: "",
            },
          },
          price: 0,
          isFullTank: false,
          isNeedChildChair: false,
          isRightWheel: false,
        })
      );
    });
  } catch (e) {
    console.error(e);
  }
};

