const defaultState = {
  orders: [],
  orderStatus: [],
  car: "",
  city: "", 
  status: "",
  page: 1,
  amountOrders: 0,
  newOrder: {
    orderStatusId: {
      id: "",
      name: "",
    },
    cityId: {
      id: "",
      name: "",
    },
    pointId: {
      id: "",
      cityId: "",
      address: "",
      name: "",
    },
    carId: {
      id: "",
      name: "",
      priceMin: 0,
      priceMax: 0,
      description: "",
      colors: [],
      thumbnail: {
        path: "",
        size: 0,
        mimetype: "",
        originalname: "",
      },
      categoryId: {
        id: "",
        name: "",
      },
    },
    color: "",
    dateFrom: new Date(),
    dateTo: new Date(),
    rateId: {
      rateTypeId: {
        id: "",
        name: "",
      },
    },
    price: 0,
    isFullTank: false,
    isNeedChildChair: false,
    isRightWheel: false,
  },
};

export const GET_ORDERS = "getOrders";
export const GET_ORDERS_STATUS = "getOrdersStatus";
export const CREATE_ORDER = "createOrder";
export const EDIT_PAGE = "editPage";
export const GET_ORDERS_AMOUNT = "getOrdersAmount";
export const EDIT_PRICE = "editPrice";
export const EDIT_CAR = "editCar";
export const EDIT_STATUS = "editStatus";
export const EDIT_CITY = "editCity";
export const EDIT_POINT = "editPoint";

export const EDIT_DATE_FROM = "editDateFrom";
export const EDIT_DATE_TO = "editDateTo";

export const EDIT_RATE = "editRate";
export const EDIT_TANK = "editTank";
export const EDIT_CHAIR = "editChair";
export const EDIT_WHEEL = "editWheel";
export const SET_CAR = "setCar";
export const SET_CITY = "setCity";
export const SET_STATUS = "setStatus";

export default (state = defaultState, { type, payload }) => {
  switch (type) {
    case GET_ORDERS:
      return {
        ...state,
        orders: payload,
      };
    case GET_ORDERS_AMOUNT:
      return {
        ...state,
        amountOrders: payload,
      };
    case GET_ORDERS_STATUS:
      return {
        ...state,
        orderStatus: payload,
      };
    case CREATE_ORDER:
      return {
        ...state,
        newOrder: payload,
      };

    case EDIT_PAGE:
      return {
        ...state,
        page: payload,
      };
    case EDIT_PRICE:
      return {
        ...state,
        newOrder: { ...state.newOrder, price: payload },
      };
    case EDIT_CITY:
      return {
        ...state,
        newOrder: {
          ...state.newOrder,
          cityId: { ...state.newOrder.cityId, id: payload },
        },
      };
    case EDIT_POINT:
      return {
        ...state,
        newOrder: {
          ...state.newOrder,
          pointId: { ...state.newOrder.pointId, id: payload },
        },
      };
    case EDIT_CAR:
      return {
        ...state,
        newOrder: {
          ...state.newOrder,
          carId: { ...state.newOrder.carId, id: payload },
        },
      };

    case EDIT_STATUS:
      return {
        ...state,
        newOrder: {
          ...state.newOrder,
          orderStatusId: { ...state.newOrder.orderStatusId, id: payload },
        },
      };
    case EDIT_DATE_FROM:
      return {
        ...state,
        newOrder: { ...state.newOrder, dateFrom: payload },
      };
    case EDIT_DATE_TO:
      return {
        ...state,
        newOrder: { ...state.newOrder, dateTo: payload },
      };
    case EDIT_RATE:
      return {
        ...state,
        newOrder: {
          ...state.newOrder,
          rateId: {
            ...state.newOrder.rateId,
            rateTypeId: { ...state.newOrder.rateTypeId, id: payload },
          },
        },
      };
    case EDIT_TANK:
      return {
        ...state,
        newOrder: { ...state.newOrder, isFullTank: payload },
      };

    case EDIT_CHAIR:
      return {
        ...state,
        newOrder: { ...state.newOrder, isNeedChildChair: payload },
      };
    case EDIT_WHEEL:
      return {
        ...state,
        newOrder: { ...state.newOrder, isRightWheel: payload },
      };
      
      case SET_CAR:
        return {
          ...state,
          car:  payload ,
        };
        case SET_CITY:
        return {
          ...state,
          city:  payload ,
        };
        case SET_STATUS:
        return {
          ...state,
          status:  payload ,
        };
    default:
      return state;
  }
};
