const defaultState = {
  rates: [],
  ratesTypes:[],
  newRate: {
    rateTypeId: {
      unit: "",
      name: "",
    },
    price: 0,
  },
};

export const GET_RATES = "getRates";
export const GET_RATES_TYPES = "getRatesTypes";
export const CREATE_RATE = "createRate";
export const EDIT_NAME = "editName";
export const EDIT_PRICE = "editPrice";

export default (state = defaultState, { type, payload }) => {
  switch (type) {
    case GET_RATES:
      return {
        ...state,
        rates: payload,
      };

    case GET_RATES_TYPES:
      return {
        ...state,
        ratesTypes: payload,
      };

    case CREATE_RATE:
      return {
        ...state,
        newRate: payload,
      };
    case EDIT_NAME:
      return {
        ...state,
        newRate: { ...state.newRate, rateTypeId: payload },
      };
    case EDIT_PRICE:
      return {
        ...state,
        newRate: { ...state.newRate, price: payload },
      };
    
    default:
      return state;
  }
};
